import { Fragment } from "react";

function DeleteWorkspace(props) {
  return (
    <div className="createbot-modal" onClick={() => props.close()}>
      <div className="craetebot-container" onClick={(e) => e.stopPropagation()}>
        <div className="success-cont">
          <div className="err-header">
            <img src="https://res.cloudinary.com/dnag1wvx8/image/upload/v1663143770/alert_icon_group_kn5l4a.svg" alt="" />
          </div>
          <div className="succ-footer">
            <h1>Delete {props.name}</h1>
            <div className="err-ft-p">
              <p>Are you sure you want to delete the {props.name}?</p>
              {(props.name === "Chatbot" || props.name === "Workflow") && (
                <p>Your {props.name} will be permanently removed and this action cannot be undone.</p>
              )}
            </div>
            <div className="dlt-wrk-btns">
              <button onClick={props.close}>Cancel </button>
              <button
                className="wrk-dlt"
                onClick={() => {
                  props.delete();
                  props.close();
                }}
              >
                Delete
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
export default DeleteWorkspace;
